<script>
import { gotoAssignUrl } from '@/utils/jsApiV2/deeplink';
import { quitCurrentWebview, getDeviceInfo } from '@/utils/jsApiV2/client';
import GrayMask from '@/components/GrayMask';

const url = 'mimarket://browse?url=https%3A%2F%2Fnileapi.market.xiaomi.com%2Fhd%2Fluck-draw%2Findex.html%3Fidentifier%3D45c050dcb9e8c96871675205b7aace41%26activityType%3Dwantstar%26lo%3DCN%26la%3Dzh%26webLaunchMode%3D2%26n_backgroundColor%3D0b0931%26a_backgroundColor%3D0b0931%26a_windowBackgroundColor%3D0b0931%26a_darkMode%3Dtrue%26s_darkMode%3Dtrue%26s_layoutAsHide%3Dtrue%26refSuffix%3Dyear10%26title%3D%E7%82%B9%E4%BA%AE%E5%B0%8F%E7%B1%B3%E5%8D%81%E5%91%A8%E5%B9%B4';

export default {
    name: 'TenYearGround',
    data() {
        return {
            modal: false,
        };
    },
    components: {
        GrayMask,
    },
    mounted() {
        this.$root.hidePlaceholder();
        // 区分小米机型与非小米机型，非小米机型为空字符串
        if (!getDeviceInfo().miui_version) {
            this.modal = true;
        }
    },
    methods: {
        goTenYearGround() {
            gotoAssignUrl(url);
        },
        goBack() {
            this.modal = false;
            quitCurrentWebview();
        },
    },
};
</script>

